

/* variant:boat-lifestyle-video-card starts */

.card.boat-lifestyle-video-card{
    @apply border-solid border-2 border-[#eef0f4] relative max-w-[240px] rounded-xl overflow-hidden flex flex-col items-center text-center;
}

.card.boat-lifestyle-video-card .imageWrapper{
    @apply aspect-square mt-[-25px] bg-white p-1;
}

.card.boat-lifestyle-video-card .detailsSection{
    @apply pt-6 pb-4 px-2;
}

.card.boat-lifestyle-video-card .detailsSection .heading{
    @apply text-16 font-bold;
}

.card.boat-lifestyle-video-card .detailsSection .price{
    @apply mt-6;
}
.card.boat-lifestyle-video-card .detailsSection .price .discountPrice{
    @apply inline-block text-16 font-bold mr-2 leading-none;
}
.card.boat-lifestyle-video-card .detailsSection .price .actualPrice{
    @apply inline-block text-14  mr-2 text-[#b2b9bf] line-through leading-none;
}
.card.boat-lifestyle-video-card .detailsSection .price .discountPercentage{
    @apply inline-block text-14  text-[#12b985] leading-none;
}

/* variant:boat-lifestyle-video-card ends */